import React, { Component } from 'react';
import '../LandingPage.css';
import 'tachyons';
import _ from 'lodash';
import { connect } from 'react-redux';

import { makeStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import menu from '../../img/menu.png';
import bird from '../../img/bird.png';


class
AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      menu: false,
      showSections:false,
      showDocs:false,
    };
    this.togglePagesMenu = this.togglePagesMenu.bind(this);
  }

  togglePagesMenu() {
    this.setState({ menu: !this.state.menu });
  }
  toggleSectionsMenu() {
    this.setState({ showSections: !this.state.menu ,menu:false,showDocs:false});
  }
  toggleDocsMenu() {
    this.setState({ showDocs: !this.state.menu,menu:false,showSections:false });
  }

  render() {
    return (
      <nav className="navbar navbar-main navbar-expand-lg navbar-transparent navbar-dark bg-dark" id="navbar-main">
        <div className="container px-lg-0">
          {/* <!-- Logo --> */}
          <a className="navbar-brand mr-lg-5" href="/">
            <img src={bird} id="navbar-logo" style={{ height: '50px' }} />
          </a>
          {/* <!-- Navbar collapse trigger --> */}
          <button className="navbar-toggler pr-0" type="button" data-toggle="collapse" data-target="#navbar-main-collapse" aria-controls="navbar-main-collapse" aria-expanded="false" aria-label="Toggle navigation" onClick={this.togglePagesMenu.bind(this)}>
            <span className="navbar-toggler-icon" />
          </button>
          {/* <!-- Navbar nav --> */}
          <div className="collapse navbar-collapse" id="navbar-main-collapse">
            <ul className="navbar-nav align-items-lg-center">
              {/* <!-- Home - Overview  --> */}
              <li className="nav-item active">
                <a className="nav-link" href="/">Overview</a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    );
  }
}

// console.log("in the admin mstp func", state)
const mapStateToProps = (state) => ({

});
const mapDispatchToProps = (dispatch) => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(AdminPage);
